//  
const loadingTrue = (info) => {
  const INFO = typeof info === 'string' ? info : 'loading'
  return {
    type: 'LOADING',
    payload: INFO
  }
}

const loadingFalse = (info) => {
  const INFO = typeof info === 'string' ? info : 'loading'
  return {
    type: 'LOADING_OFF',
    payload: INFO
  }
}

